.about-block{
    padding: 60px 0 70px;
    position: relative;
    .section-title{
        text-align: left;
        color: #ffffff;
        margin-bottom: 0;
    }
    .typography{
        color: rgba(255, 255, 255, 0.502);
        line-height: 1.588;
        font-size: 13px;
        margin-top: 30px;
    }
    .Btn{
        margin-top: 60px;
    }
}
.about-block__inner {
    position: relative;
    z-index: 1;
}
@media(max-width: 1890px){
    .about-block::after{
        right: -15px;
    }
}
@media(max-width: 1199px) {
    .about-block {
        padding-top: 40px;
    }
}
@media(max-width: 1024px){
    .about-block {
        padding: 0 0 40px;
        &::after{
            top: auto;
            bottom: 18px;
        }
        .Btn{
            margin-top: 30px;
        }
    }
}
@media(max-width: 767px){
    .about-block{
        .typography{
            font-size: 14px;
        }
    }
}
@media(max-width: 600px){
    .about-block {
        padding-bottom: 30px;
        &::after{
            display: none;
        }
        .typography{
            margin-top: 15px;
        }
        .Btn{
            margin-top: 12px;
        }
    }
}